import React, { useState } from "react";

import { MainTitle, TitleWrapper, } from "../TitleSection";
import { translations, tags, publiTypes, publications } from "./data";
import PublicationItem from "./PublicationItem";
import {
  PublicationsContainer,
  Dropdown,
  DropdownBtn,
  DropdownContent,
  DropdownItem,
  TagWrapper,
  DropdownsWrapper,
  TagItem,
  PublicationsWrapper,
} from "./PublicationsElements";
import TagList from "./TagList";

const Publications = ({ french }) => {
  const [activeTags, setActiveTags] = useState([]);
  const [showTags, setShowTags] = useState(false);
  const [showTypes, setShowTypes] = useState(false);
  const [showSort, setShowSort] = useState(false);
  const [activeType, setActiveType] = useState(0);
  const [activeTypes, setActiveTypes] = useState([]);
  const [activeSort, setActiveSort] = useState(0);
  const [activeSorts, setActiveSorts] = useState([0]);

  const sortTags = [
    { id: 1, fr: "Date", en: "Date" },
    { id: 2, fr: "Type", en: "Type" },
  ];

  const addTag = (tag) => {
    setShowTags(false);
    const tagId = tag["id"];
    const index = activeTags.indexOf(tagId);
    // if (index === -1) {
    //   if (activeTags.length === tags.length - 1) {
    //     setActiveTags([]);
    //   } else {
    //     setActiveTags([...activeTags, tagId]);
    //   }
    // } else {
    //   setActiveTags(
    //     activeTags.filter((i) => {
    //       return i !== tagId;
    //     })
    //   );
    // }

    if (index === -1) {
      setActiveTags([...activeTags, tagId]);
    }
  };

  const onClickTag = (tag) => {
    const tagId = tag["id"];
    const index = activeTags.indexOf(tagId);
    if (index === -1) {
      setActiveTags([...activeTags, tagId]);
    } else {
      setActiveTags(
        activeTags.filter((i) => {
          return i !== tagId;
        })
      );
    }
  };

  const onClickTypes = (tag) => {
    const tagId = tag["id"];
    const index = activeTypes.indexOf(tagId);
    if (index === -1) {
      setActiveTypes([...activeTypes, tagId]);
    } else {
      setActiveTypes(
        activeTypes.filter((i) => {
          return i !== tagId;
        })
      );
    }
  };

  const onClickSort = (tag) => {
    const tagId = tag["id"];
    // const index = activeTypes.indexOf(tagId);
    // if (index === -1) {
    //   setActiveSorts([tagId]);
    // } else {
    //   setActiveSorts([]);
    // }
    if (activeSorts[0] === tag["id"]) {
      setActiveSorts([0]);
    } else {
      setActiveSorts([tagId]);
    }
  };

  const removeTag = (tag) => {
    // const tagId = tag["id"];
    // const index = activeTags.indexOf(tagId);
    // if (index === -1) {
    //   if (activeTags.length === tags.length - 1) {
    //     setActiveTags([]);
    //   } else {
    //     setActiveTags([...activeTags, tagId]);
    //   }
    // } else {
    //   setActiveTags(
    //     activeTags.filter((i) => {
    //       return i !== tagId;
    //     })
    //   );
    // }

    setActiveTags(
      activeTags.filter((i) => {
        return i !== tag["id"];
      })
    );
  };

  const displayTag = (tagId) => {
    // if (activeTags.length === 0) {
    //   return true;
    // } else if (activeTags.indexOf(tagId) === -1) {
    //   return false;
    // }

    if (activeTags.indexOf(tagId) === -1) {
      return false;
    }

    return true;
  };

  const containsTag = (tagList) => {
    if (activeTags.length === 0) return true;
    let index = 0;
    while (index < activeTags.length) {
      if (tagList.indexOf(activeTags[index]) !== -1) return true;
      index++;
    }
    return false;
  };

  const containsType = (type) => {
    if (activeTypes.length === 0) return true;
    if (activeTypes.indexOf(type) === -1) {
      return false;
    }

    return true;
  };

  const showPubli = (p) => {
    if (!containsTag(p["tags"])) {
      return false;
    }
    // if (activeType === 0) {
    //   return true;
    // }
    // return p["publiType"] === activeType;
    return containsType(p["publiType"]);
  };

  const typeName = (t) => {
    if (t === 0) {
      return "Types";
    } else {
      return publiTypes.find((x) => x.id === t)["fr"];
    }
    // publiTypes[t - 1]["fr"];
  };

  const listPubliType = (type) => {
    // const list = publications.map(
    //   (p) =>
    //     showPubli(p) &&
    //     p["publiType"] === type && (
    //       <PublicationItem key={p["id"]} publi={p} tags={tags} />
    //     )
    // );
    const list = [];
    let empty = true;
    for (const p of publications) {
      if (showPubli(p) && p["publiType"] === type) {
        list.push(
          <PublicationItem
            key={p["id"]}
            publi={p}
            tags={tags}
            french={french}
          />
        );
        empty = false;
      }
    }
    if (empty) return false;
    else return list;
  };

  const getListDate = () => {
    let list = [];
    for (const p of publications) {
      if (list.indexOf(p.date) === -1) {
        list.push(p.date);
      }
    }
    return list;
  };

  const listPubliDate = (date) => {
    const list = [];
    let empty = true;
    for (const p of publications) {
      if (showPubli(p) && p["date"] === date) {
        list.push(
          <PublicationItem
            key={p["id"]}
            publi={p}
            tags={tags}
            french={french}
          />
        );
        empty = false;
      }
    }
    if (empty) return false;
    else return list;
  };

  const publicationList = () => {
    if (activeSorts[0] === 0) {
      return publications.map(
        (p) =>
          showPubli(p) && (
            <PublicationItem
              key={p["id"]}
              publi={p}
              tags={tags}
              french={french}
            />
          )
      );
    } else if (activeSorts[0] === 2) {
      return publiTypes.map(
        (p) =>
          listPubliType(p["id"]) && (
            <div key={p["id"]}>
              <h3>{p["fr"]}</h3>
              {listPubliType(p["id"])}
            </div>
          )
      );
    } else if (activeSorts[0] === 1) {
      let listDate = getListDate();
      listDate = listDate.sort(function (a, b) {
        return b - a;
      });
      return listDate.map(
        (d) =>
          listPubliDate(d) && (
            <div key={d}>
              <h3>{d}</h3>
              {listPubliDate(d)}
            </div>
          )
      );
    }
  };

  return (
    <PublicationsContainer>
      <TitleWrapper>
        <MainTitle>{translations[french ? "fr" : "en"]["title"]}</MainTitle>
      </TitleWrapper>
      {/* <TitleSection>{translations[french ? "fr" : "en"]["title"]}</TitleSection> */}
      {/* <TagList
        title={"Tags :"}
        tagList={tags}
        activeTags={activeTags}
        onClickTag={onClickTag}
      />
      <TagList
        title={"Types :"}
        tagList={publiTypes}
        activeTags={activeTypes}
        onClickTag={onClickTypes}
      />
      <TagList
        title={"Trier par :"}
        tagList={sortTags}
        activeTags={activeSorts}
        onClickTag={onClickSort}
      /> */}
      {/* <DropdownsWrapper>
        <Dropdown
          onMouseLeave={() => {
            setShowTags(false);
          }}
        >
          <DropdownBtn
            onMouseEnter={() => {
              setShowTags(true);
            }}
          >
            Tags
          </DropdownBtn>

          <DropdownContent showItems={showTags}>
            {tags.map(
              (t) =>
                !displayTag(t["id"]) && (
                  <DropdownItem
                    key={t["id"]}
                    onClick={() => {
                      addTag(t);
                    }}
                  >
                    {t["fr"]}
                  </DropdownItem>
                )
            )}
          </DropdownContent>
        </Dropdown>

        <Dropdown
          onMouseLeave={() => {
            setShowTypes(false);
          }}
        >
          <DropdownBtn
            onMouseEnter={() => {
              setShowTypes(true);
            }}
          >
            {typeName(activeType)}
          </DropdownBtn>
          <DropdownContent showItems={showTypes}>
            {publiTypes.map((t) => (
              <DropdownItem
                key={t["id"]}
                onClick={() => {
                  setActiveType(t.id);
                }}
              >
                {t["fr"]}
              </DropdownItem>
            ))}
            <DropdownItem
              onClick={() => {
                setActiveType(0);
              }}
            >
              Tout
            </DropdownItem>
          </DropdownContent>
        </Dropdown>

        <Dropdown
          onMouseLeave={() => {
            setShowSort(false);
          }}
        >
          <DropdownBtn
            onMouseEnter={() => {
              setShowSort(true);
            }}
          >
            Sort
          </DropdownBtn>
          <DropdownContent showItems={showSort}>
            <DropdownItem
              onClick={() => {
                setActiveSort(1);
              }}
            >
              Date
            </DropdownItem>
            <DropdownItem
              onClick={() => {
                setActiveSort(2);
              }}
            >
              Type
            </DropdownItem>
            <DropdownItem
              onClick={() => {
                setActiveSort(0);
              }}
            >
              Aucun
            </DropdownItem>
          </DropdownContent>
        </Dropdown>
      </DropdownsWrapper> */}
      {/* <TagWrapper>
        {tags.map(
          (t) =>
            displayTag(t["id"]) && (
              <TagItem
                key={t["id"]}
                // active={displayTag(t["id"])}
                onClick={() => {
                  removeTag(t);
                }}
              >
                {t["fr"]}
              </TagItem>
            )
        )}
      </TagWrapper> */}
      
      <br />
      {/* <ul>
        {publications.map((p) => (
          <li key={p["id"]}>
            {p["fr"]["title"]}, {p["fr"]["journal"]}
            <br />
            Tags:
            <ul>
              {p["tags"].map((t) => (
                <li key={t}>{t}</li>
              ))}
            </ul>
          </li>
        ))}
      </ul>
      <br /> */}
      <PublicationsWrapper>
      <ul>{publicationList()}</ul>
      </PublicationsWrapper>
      
      {/* <ul>
        {tags.map(
          (t) =>
            activeTags.indexOf(t["id"]) === -1 && (
              <li key={t["id"]}>{t["fr"]}</li>
            )
        )}
      </ul> */}
      {/* <br />
      <ul>
        {tags.map(
          (t) => displayTag(t["id"]) && <li key={t["id"]}>{t["fr"]}</li>
        )}
      </ul> */}
    </PublicationsContainer>
  );
};

export default Publications;
