import React, { useState } from "react";
import { MathJax, MathJaxContext } from "better-react-mathjax";
import { translations } from "./data";
import {
  PublicationButtonPdf,
  PublicationButtonResume,
  PublicationInfos,
  PublicationNumber,
  PublicationRevue,
  PublicationTags,
  PublicationTitle,
  PublicationWrapper,
  PublicationButtonWrapper,
  PublicationClickArea,
  Abstract,
  PublicationCoAuthor,
  TextMobile,
  PublicationNumberTransparent,
} from "./PublicationItemElements";
import {
  AiFillFilePdf,
  AiOutlinePlusCircle,
  AiOutlineMinusCircle,
} from "react-icons/ai";

const PublicationItem = ({ publi, tags, french }) => {
  const [showResume, setShowResume] = useState(false);

  const config = {
    loader: { load: ["[tex]/html"] },
    tex: {
      packages: { "[+]": ["html"] },
      inlineMath: [
        ["$", "$"],
        ["\\(", "\\)"],
      ],
      displayMath: [
        ["$$", "$$"],
        ["\\[", "\\]"],
      ],
    },
  };

  const tagName = (tagId) => {
    return tags.find((x) => x["id"] === tagId)[french ? "fr" : "en"];
  };

  const changeResume = () => {
    setShowResume(!showResume);
  };
  return (
    <>
      <PublicationWrapper>
        <PublicationClickArea onClick={changeResume} className={"link"}>
          <PublicationNumber>{publi["id"]}</PublicationNumber>
          <PublicationInfos>
            <PublicationTitle>
              <MathJaxContext version={3} config={config}>
                {publi[french ? "fr" : "en"]["title"]}
              </MathJaxContext>
            </PublicationTitle>
            <PublicationRevue>
              {publi[french ? "fr" : "en"]["journal"]}
            </PublicationRevue>
            <PublicationCoAuthor>
              {publi["coAuthors"].length > 0
                ? [
                    translations[french ? "fr" : "en"]["avec"],
                    publi["coAuthors"].map((t, i) => [i > 0 && ", ", t]),
                  ]
                : ""}
              {/* Avec : {publi["coAuthors"].map((t, i) => [i > 0 && ", ", t])} */}
            </PublicationCoAuthor>
            <PublicationTags>
            {translations[french ? "fr" : "en"]["motcles"]} {publi["tags"].map((t, i) => [i > 0 && ", ", tagName(t)])}
            </PublicationTags>

            
          </PublicationInfos>


        </PublicationClickArea>
        

        
        <PublicationButtonWrapper>
          <PublicationNumberTransparent>{publi["id"]}</PublicationNumberTransparent>
          <PublicationButtonPdf href={"./publications/" + publi["file"]}>
          <TextMobile className={"link"}>Pdf</TextMobile>
            <AiFillFilePdf />
          </PublicationButtonPdf>
          <PublicationButtonResume onClick={changeResume} className={"link"}>
            <TextMobile>{translations[french ? "fr" : "en"]["resume"]}</TextMobile>
            {showResume ? <AiOutlineMinusCircle /> : <AiOutlinePlusCircle />}
          </PublicationButtonResume>
        </PublicationButtonWrapper>
      </PublicationWrapper>

      {showResume && (
        <Abstract>
          <MathJaxContext version={3} config={config}>
            {/* <MathJax>{"\\(\\frac{10}{4x} \\approx 2^{12}\\)"}</MathJax> */}
            <MathJax> {translations[french ? "fr" : "en"]["resume2"]}  {publi[french ? "fr" : "en"]["abstract"]}</MathJax>
          </MathJaxContext>
        </Abstract>
      )}
      <br />
    </>
  );
};

export default PublicationItem;
