import React from "react";
import { MainTitle, TitleWrapper, } from "../TitleSection";
import {
  ExposesContainer,
  ExposesDate,
  ExposesContent,
  ExposesWrapper,
  ExposesItemsWrapper,
} from "./ExposesElements";
import { translations, talks } from "./data";

const Exposes = ({ french }) => {
  return (
    <ExposesContainer>
      <TitleWrapper>
        <MainTitle>{translations[french ? "fr" : "en"]["title"]}</MainTitle>
      </TitleWrapper>
      
      <ExposesWrapper>
        <ExposesItemsWrapper>
          {talks.map((c) => (
            <div key={c["id"]}>
              <ExposesDate>{c[french ? "fr" : "en"]["date"]}</ExposesDate>
              <ExposesContent>{c[french ? "fr" : "en"]["title"]}</ExposesContent>
            </div>
          ))}
        </ExposesItemsWrapper>
      </ExposesWrapper>
    </ExposesContainer>
  );
};

export default Exposes;
